.messages{
    display: grid;
    grid-template-columns: 10fr 2fr;
}

.dialog{
    background-color: #ffffff;
    height: 100vh;
    border-right: 1px solid #EBEBEB;
}
.dialogItems{
    background-color: #ffffff;
}
.dialogHead{
    border-bottom: 1px solid #EBEBEB;
    min-height: 80px;
}
.dialogBody{
    padding: 50px;
}
.dialogSearch{
    border-bottom: 1px solid #EBEBEB;
    min-height: 80px;
}



.messageOut{
    margin-left: auto;
    background-color: #E3EFFF;
}